const navDropdowns = document.querySelectorAll('.uk-navbar-dropdown');

for (let navDropdown of navDropdowns) {
  navDropdown.parentNode.children[0]
      .addEventListener(
          'mouseleave',
          function () {
            navDropdown.classList.remove('uk-open')
          });
  navDropdown.parentNode.children[0]
      .addEventListener(
          'mouseover',
          function () {
            navDropdown.classList.add('uk-open')
          });
  navDropdown
      .addEventListener(
          'mouseleave',
          function () {
            navDropdown.classList.remove('uk-open')
          });
  navDropdown
      .addEventListener(
          'mouseover',
          function () {
            navDropdown.classList.add('uk-open')
          });
}

const titleList = document.querySelectorAll('.mission-title-list-item');
let current = 0;

setInterval(
    () => {
      titleList[current].classList.remove('mission-title-list-item--show');
      current = (current + 1) % titleList.length;
      titleList[current].classList.add('mission-title-list-item--show');
    },
    2500
);

const garmFormItems = document.querySelectorAll('.form-input');

for (let formItem of garmFormItems) {
  formItem.children[0].addEventListener('input', function () {
    if (!this.value) {
      formItem.children[1].classList.remove('label--active');
      return;
    }

    formItem.children[1].classList.add('label--active');
  });

  if (formItem.children[0].value) {
    formItem.children[1].classList.add('label--active');
  }
}
